body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.app {
  margin: 10px;
}

ul {
  margin: 0;
  padding-left: 0;
}

li {
  margin-top: 10px;
  list-style: none;
}

li .title {
  display: inline-block;
  width: 300px;
  border: 1px solid #666;
  padding: 10px;
  border-radius: 10px;
}

li .date {
  display: inline-block;
  margin-left: 10px;
  font-family: 'Courier New', Courier, monospace;
  font-weight: 900;
  color: green;
}

li .date.alert {
  color: red;
}

li .date.warning {
  color: orange;
}

li .button {
  font-size: 1.7rem;
  margin-left: 10px;
  opacity: 0;
  cursor: pointer;
}

li:hover .title {
  background-color: #eee;
}

li:hover .button, li .button.active {
  opacity: 1;
}
